.click {
    position: absolute;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding-top: 80px;
    background: rgba(0,0,0,0.4);
    display: flex;
    justify-content: center;
    cursor: pointer;
    opacity: 0;
    z-index: 999;
    
    &:hover {
        opacity: 99;
    }
}

.hasAdd {
    position: absolute;
    top: 0px;
    left:0;
    width: 80px;
    height: 30px;
    box-sizing: border-box;
    opacity: 1;
    z-index: 1;
}

.bg {
    background-color: #fff !important;
}